.right {
  text-align: right;
}

.left {
  text-align: left;
}

.center {
  text-align: center;
}

.bold {
  font-weight: 700;
}

.semi-bold {
  font-weight: 600;
}

.light-bold {
  font-weight: 500;
}

.title {
  font-size: 17px;
  line-height: 16px;
  font-weight: 600;
  padding-bottom: 14px !important;
}

.sub-title {
  font-weight: 600;
  font-size: 22px;
  line-height: 16px;
  letter-spacing: 0.2px;
  padding-bottom: 22px;
}

.normal {
  font-weight: normal;
}

.fs-13 {
  font-size: 13px;
}

.fs-15 {
  font-size: 15px;
}

.fs-20 {
  font-size: 20px;
}

.fs-30 {
  font-size: 30px;
}

.fs-45 {
  font-size: 45px;
}

.upper {
  text-transform: uppercase;
}

.no-select {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}
