@font-face {
  font-family: 'Graphik';
  src: local('Graphik'), url(../fonts/GraphikRegular.otf) format('opentype');
}

@font-face {
  font-family: 'Graphik';
  font-weight: 500;
  src: local('Graphik'), url(../fonts/GraphikMedium.otf) format('opentype');
}

@font-face {
  font-family: 'Graphik';
  font-weight: 600;
  src: local('Graphik'), url(../fonts/GraphikSemibold.otf) format('opentype');
}

@font-face {
  font-family: 'Graphik';
  font-weight: 700;
  src: local('Graphik'), url(../fonts/GraphikBold.otf) format('opentype');
}

@font-face {
  font-family: 'Graphik';
  font-weight: 900;
  src: local('Graphik'), url(../fonts/GraphikBlack.otf) format('opentype');
}

body {
  font-family: Graphik, sans-serif;
  font-size: 14px;
  color: #000;
  background: $background-color;
  padding: 0;
  margin: 0;
}

* {
  box-sizing: border-box;
}

::placeholder {
  color: $placeholder-color;
  opacity: 1;
}

input,
textarea {
//select {
  padding: 10px;
  font-family: Graphik, sans-serif;
  font-size: 14px;
  color: #000;
  background: transparent;
  border-radius: 5px;
  outline: 0;
}

//select {
//  outline: 0;
//}

button {
  font-family: Graphik, sans-serif;
  font-size: 14px;
  border-radius: 3px;
  cursor: pointer;
}

textarea {
  resize: vertical;
  background: transparent;
}

button.link {
  display: flex;
  align-items: center;
  background: transparent;
  border: 0;
  font-size: 17px;
  font-weight: 500;
  color: $color-blue;

  &:hover,
  &:focus {
    text-decoration: underline;
  }

  &:focus {
    outline: none;
    opacity: 1;
  }
}

button.add {
  border: 1px dashed $color-dimmed;
  border-radius: 5px;
  padding: 11px;
  background: $color-sky;
  font-size: 15px;
  font-weight: 400;
  color: #0075FF;

  &:focus {
    outline: none;
    opacity: 1;
  }
}

h1 {
  margin-top: 0;
  margin-bottom: 30px;
}

label {
  color: $color-dimmed;
}
