.block {
  display: block;
}

.flex {
  display: flex !important;
}

.justify-space-between {
  justify-content: space-between;
}
.justify-end {
  justify-content: flex-end;
}

.flex-col {
  flex-direction: column;
}

.color-gray-text {
  color: $color-gray-text;
}

.align-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}
.m-auto {
  margin: auto;
}

.w-auto {
  width: auto !important;
}

.w-100 {
  width: 100% !important;
}
.gap-30 {
  gap: 30px;
}

.w-50 {
  width: 50%;
}

.w-55 {
  width: 55%;
}

.w-45 {
  width: 45%;
}

.w-60 {
  width: 60%;
}

.w-40 {
  width: 40%;
}

.w-38 {
  width: 31%;
}

.w-48 {
  width: 48%;
}

.w-17 {
  width: 17%;
}

.word-break {
  word-break: break-word;
}

.w-18 {
  width: 18%;
}

.row {
  position: relative;
}

.row__remove {
  display: flex;
  padding: 0;
  position: absolute;
  top: 10px;
  right: -20px;

  .row:hover &,
  .row:focus-within & {
    opacity: 0.8;
  }
}
