.modal {
  display: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  background: rgba(0, 4, 39, 0.15);
  animation: fadeIn 300ms ease forwards 1;
  z-index: 9999;
  display: flex;
  position: fixed;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.header {
  background: #f4f4f5;
  width: 100%;
  height: 64px;

  font-weight: 600;
  font-size: 22px;
  line-height: 16px;
  padding: 24px 29px;
  border-radius: 10px 10px 0px 0px;

  display: flex;
  align-items: center;
  justify-content: space-between;
  letter-spacing: 0.2px;
}

.inner {
  --action-border-radius: 12px;
  position: relative;
  margin: 0 auto;
  background-color: #fff;
  max-width: 634px;
  animation: fadeIn 300ms ease forwards 1;
  border-radius: 10px;
  min-width: 240px;
  max-height: calc(100% - 48px);
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  display: flex;
  flex-direction: column;

  &.overflowDisable {
    overflow: visible;
  }

  .innerContent {
    padding: 64px 40px 43px 40px;
  }

  .closeIcon {
    cursor: pointer;

    &:hover {
      opacity: 0.8;
    }
  }
  label {
    margin-bottom: 6px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;

    color: #000000;

    mix-blend-mode: normal;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  50% {
    opacity: 0.5;
  }

  to {
    opacity: 1;
  }
}
